import React from "react";
import axios from 'axios';
import { useEffect, useState } from 'react';
import "../App.css";
import "../head.css";
import Head from "../Header";
import { RadialChart } from 'react-vis';




function MainPage() {

    const [token, setToken] = useState("")

    useEffect(() => {

        const hash = window.location.hash
        let tok = window.localStorage.getItem("token")

        if (hash) {
            tok = hash.substring(1).split("&").find(elem => elem.startsWith("access_token")).split("=")[1]

            window.location.hash = ""
            window.localStorage.setItem("token", tok)
        }

        setToken(tok)

    }, [])

    useEffect(() => {
        searchArtistsHome();
    }, [token])



    // I -- MOSTRA TODAS SUAS PLAYLISTS
    const [playlists, setPlaylists] = useState([])

    const searchPlaylists = async (e) => {
        // e.preventDefault()
        const { data } = await axios
            .get(
                'https://api.spotify.com/v1/me/playlists', {
                params: { offset: 0 },
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': 'Bearer ' + token,
                },
            })

        setPlaylists(data.items)
        
    }

    const renderPlaylists = () => {
        return playlists.map(playlist => (
            <article>
                <p className="showName">{playlist.name}</p>
                {playlist.images.length ? <img className="showImage" width={"40%"} src={playlist.images[0].url} alt="" /> : <div>No Image</div>}
            </article>
        ))
    }
    // F -- MOSTRA TODAS SUAS PLAYLISTS


    // I -- MOSTRA TOP 3 ARTISTAS
    const [artists, setArtists] = useState([])

    const searchArtistsHome = async (e) => {
        // e.preventDefault()

        const { data } = await axios.get("https://api.spotify.com/v1/me/top/artists", {
            headers: {
                Authorization: `Bearer ${token}`,
                Accept: 'application/json',
                'Content-Type': 'application/json'
            },
            params: {
            }
        })
        generosCalc(data.items);
        setArtists(data.items);
    }

    const renderTop3Artists = () => {
        return artists.slice(0, 3).map(artist => (
            <article>
                <p className="showName">{artist.name}</p>
                {artist.images.length ? <img className="showImage" width={"40%"} src={artist.images[0].url} alt="" /> : <div>No Image</div>}
            </article>
        ))
    }
    // F -- MOSTRA TOP 3 ARTISTAS

    // I -- CALCULAR GENEROS

    const [generos, setGeneros] = useState([]);

    const auxGeneros = []

    const generosCalc = async (data) => {

        var existe = false;

        try {

            for (let artist of data) {
                auxGeneros.forEach((nameGenre, index) => {
                    if (nameGenre.label === artist.genres[0]) {
                        auxGeneros[index].angle++;
                        existe = true;
                    }
                });

                if (!existe) {
                    auxGeneros.push({
                        label: artist.genres[0],
                        angle: 1
                    });
                }
                existe = false;

            }
        } catch (err) { }

        const opa = sortDesc(auxGeneros);


        for (let i = 0; i < 6; i++) {
            setGeneros(prevMsgs => [...prevMsgs, opa[i]])
        }

        let outros = opa.length - 5;

        if (outros > 0) {
            setGeneros(prevMsgs => [...prevMsgs, {
                label: "Outros",
                angle: outros
            }])
        }
        
        console.log(auxGeneros)
        calcRec(auxGeneros);
        return;

    }

    const sortDesc = (data) => {

        let result = data;

        for (let i = 0; i < result.length; i++) {
            for (let j = i + 1; j < result.length; j++) {

                if (result[j].angle > result[i].angle) {
                    let aux = result[i];
                    result[i] = result[j];
                    result[j] = aux;
                }
            }
        }


        // return (result.slice(0, 5));
        return data;
    }

    const renderChart = () => {

        return (
            <div className="centralizar">
                {/* {genero.label} {genero.angle} */}
                <RadialChart animation data={generos} width={300} height={300} showLabels={true} />
            </div>
        )

    }
    // F -- CALCULAR GENEROS

    // I -- PEGAR RECOMENDAÇÕES


    const [recommendations, setRec] = useState();

    // get + seed_genres -- WORKING
    // hip hop --> hip-hop

    const calcRec = (gen) => {


        let pop = '';

        try{

            console.log("debug 1")
            
            console.log(gen)
            
            pop += 'musica-triste-brasileira'
            // gen.slice(0,5).forEach((nameGenre, index) => {
            //     pop += nameGenre.label.replaceAll(' ', '-') + '%2C'; 
            // });
            // gen.slice(5,6).forEach((nameGenre, index) => {
            //     pop += nameGenre.label.replaceAll(' ', '-'); 
            // });
    
            console.log(pop)
            console.log("debug 2")
            
// ?seed_genres=
            let get = 'https://api.spotify.com/v1/recommendations';
            // get += pop;
                // get += 'pop';
                
                fetchRecom(get);
                
        } catch(err) {console.log(err)}

        console.log("fim")
    }

    const fetchRecom = async (get) => {

        const data = await axios
        // ?seed_genres=pop%2Crock
        // POR GENERO É PAIA, ALGUNS N ACEITA
            .get(
                get, {
                params: { 
                    "seed_artists": null,
                    "seed_genres": null,
                    // [
                    //     'pop',
                    //     'rock'
                    // ],
                    "seed_tracks" : null, 
                    limit: 10
                },
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': 'Bearer ' + token,
                },
            })

        
        try{console.log(data); console.log(data.data.tracks)}catch(err){console.log(err)}

        setRec(data.data.tracks);
    }

    const renderRecommendations = async () => {

        return recommendations.slice(0, 3).map(rec => (
            <article>
                <p className="showName">{rec.name}</p>
                {/* {rec.album.images.length ? <img className="showImage" width={"40%"} src={rec.album.images[0].url} alt="" /> : <div>No Image</div>} */}
            </article>
        ))

    }    
    // F -- PEGAR RECOMENDAÇÕES



    return (
        <div className="App">

            <Head />

            <main className="corpo">

                <h2>Resumo do Mês</h2>
                <br />
                <div className="showContainer">
                    {renderTop3Artists()}
                </div>


                <h2>Estatísticas</h2>
                {renderChart()}

                <h2>Recomendações</h2>
                {/* {renderRecommendations()} */}

                <br /><br /><br /><br />aaaa<br />          <br />https://developer.spotify.com/documentation/web-api/reference/#/operations/get-recommendations


            </main>

        </div >
    )

}

export default MainPage;